.item {
    border: solid;
    border-width: 1px 0;
    border-color: rgba(229, 229, 229, 1);
}

.item:last-of-type {
    border: none;
}

.users {
    border-collapse: collapse;
    width: 100%;
}

.buttonsContainer {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 18px;
    gap: 12px;
}

.container {
    max-width: 606px;
    width: 100%;
    font-size: 14px;
}

.thead {
    font-weight: 600;
}

.nameCol {
    width: 50%;
    padding: 16px 17px;
    font-weight: 300;
}

.ageCol {
    width: 50%;
    padding: 16px 17px;
    font-weight: 500;
}